//other
import { STOK_TAKE_UPLOAD_JSON } from "apiServices/action";
import { submitCsv } from "apiServices";
import moment from 'moment'

export const showErrorsOnly = ({
  csvData,
  csvField,
  isShowErrorOnly,
  setIsShowErrorOnly,
  setIsHiddenRows,
  setCheckingCsv
}) => {
  // ;
  let tmp_arr_false = [];
  let tmp_arr_true = [];
  csvData.map((data, index) => {
    let stat = true;
    let i = index + 1;
    csvField.map((dataColumn, indexColumn) => {
      let key = dataColumn.dataField + "Status";
      if (data[key] === false) {
        stat = false;
      }
    });

    if (stat === false) {
      if (!tmp_arr_false.includes(i)) {
        tmp_arr_false.push(i);
      }
    } else {
      if (!tmp_arr_true.includes(i)) {
        tmp_arr_true.push(i);
      }
    }
  });

  //return array
  let ret = [];
  if (isShowErrorOnly === true) {
    //true = delete all row
    ret = tmp_arr_true.filter((x) => !tmp_arr_false.includes(x));
    if (ret.length == csvData.length) {
      return 0;
    }
  } else {
    ret = [];
  }
  setIsHiddenRows(ret);
  setIsShowErrorOnly(!isShowErrorOnly); //untuk button show all atau error only
  setCheckingCsv(true)
};

export const deleteErrorsField = ({ csvData, csvField, dispatch, setCheckingCsv }) => {
  let new_arr = [];
  let tmp = null;
  csvData.map((data, index) => {
    let stat = true;
    csvField.map((dataColumn, indexColumn) => {
      let key = dataColumn.dataField + "Status";
      let i = index + 1;
      if (data[key] === false) {
        stat = false;
      }
    });
    if (stat === true) {
      new_arr.push(data);
    }
  });
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: new_arr });
  setCheckingCsv(true)
};

export const setCsvData = ({ param, dispatch, setCheckingCsv }) => {
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: param });
  setCheckingCsv(true)
};

export const checking_error = ({
  csvData,
  csvField,
  setIsValidate,
  setCheckingCsv
}) => {
  if (csvData === undefined) {
    return 0;
  }
  let stats = true;
  csvData.map((data, index) => {
    csvField.map((dataField, indexField) => {
      // console.log(data[dataField + 'status']);
      let status = data[dataField.dataField + 'Status'];

      //jika outbound type = lainya, maka harus isi outbound reason
      if (dataField.dataField == 'outboundType') {
        let type = data[dataField.dataField].toUpperCase();
        if (type == "LAINNYA") {
          if (data['outboundReason'] == "") {
            status = false;
          }
        }
      }

      if (status === false) {
        // console.log(data, dataField.dataField, data[dataField.dataField + 'Status']);
        stats = false;
      }
    });
  });
  if (stats === false) {
    setIsValidate(false);
  } else {
    setIsValidate(true);
  }
  setCheckingCsv(false)
}

export const submit = async ({
  csvData,
  dispatch,
  selectorStatus,
  setIsShowModal,
  setIsSubmitText,
  setSubmitStatus
}) => {
  // console.clear()
  setIsSubmitText("Loading...");
  // console.clear();
  let jobHeader = [];
  let poHeader = [];
  let poBody = [];

  //0 get time
  let time = new Date().getTime();
  let d = new Date(time);
  let n = d.getMilliseconds();

  //1. set header
  jobHeader = [
    {
      jobPost: {
        id: 0,
        warehouseSource: csvData[0].warehouseSource || "",
        warehouseDestination: csvData[0].warehouseDestination || "",
        areaSource: csvData[0].areaSource || "",
        areaDestination: csvData[0].areaDestination || "",
        list: null,
        createdAt: n
      },
    },
  ];


  //2. get sku
  let sku_uuid_array = [];
  let sku_number_array = [];
  let sku_data = [];
  csvData.map((data, index) => {
    if (!sku_uuid_array.includes(data.skuUuid)) {
      sku_uuid_array.push(data.skuUuid);
      sku_data.push({
        skuNumber: data.skuNumber,
        skuUuid: data.skuUuid,
        uom: data.uom,
        photo: null,
      });
    }
  });

  // console.clear();
  //3. get PO per sku
  let po_array = []
  sku_data.map((data_sku, index_sku) => {
    if (data_sku === null || data_sku === undefined || data_sku === "") {
      return false;
    }

    let tmp_po = {}
    tmp_po.skuUuid = data_sku.skuUuid
    tmp_po.skuNumber = data_sku.skuNumber
    tmp_po.unitOfMeassures = data_sku.uom
    tmp_po.photo = data_sku.photo
    tmp_po.poList = []
    csvData.map((data, index) => {
      if (data.skuNumber !== data_sku.skuNumber) {
        return false;
      }

      let tmp_po_array = {}
      tmp_po_array.wmsPoSkuCode = data.wmsPoSkuCode || "";
      tmp_po_array.supplierUuid = data.supplierUuid || "";
      tmp_po_array.supplierName = data.supplierName || "";
      tmp_po_array.codeGrade = data_sku.skuUuid;
      tmp_po_array.inboundDate = data.inboundDate || "";
      tmp_po_array.expiryDate = data.expiryDate || "";

      tmp_po_array.qty = data.qty || "";
      tmp_po_array.ripeness = data.ripenessLevel || 0;
      tmp_po_array.outboundTypeCode = data.outboundTypeCode || "";
      tmp_po_array.outboundReason = data.outboundReason || "";
      tmp_po_array.inboundTypeCode = data.inboundTypeCode || "";
      tmp_po.poList.push(tmp_po_array);
    });
    po_array.push(tmp_po);
  })
  // 
  // 

  //merapikan po_array
  let po_array_fix = [];
  po_array.map((data, index) => {
    let tmp = {}
    let po_code = []
    let po_list = []
    data.poList.map((data_po, index_po) => {
      if (!po_code.includes(data_po.wmsPoSkuCode)) {
        po_code.push(data_po.wmsPoSkuCode);
        let tmp_po_array = {}
        tmp_po_array.wmsPoSkuCode = data_po.wmsPoSkuCode;
        tmp_po_array.supplierId = data_po.supplierUuid;
        tmp_po_array.supplierName = data_po.supplierName;
        tmp_po_array.codeGrade = data_po.codeGrade;
        tmp_po_array.ripenessLevel = data_po.ripeness;

        //date
        tmp_po_array.inboundDate = data_po.inboundDate
        tmp_po_array.expiryDate = Date.parse(moment(data_po.expiryDate, 'DD/MM/YYYY').format('MM-DD-YYYY')) || "";

        tmp_po_array.itemList = [];
        po_list.push(tmp_po_array);
      }

      let index_po_code = po_code.indexOf(data_po.wmsPoSkuCode);
      let tmp_itemlist = {}
      tmp_itemlist.grade = { label: data_po.grade, value: data_po.grade }
      tmp_itemlist.ripeness = { label: data_po.ripeness, value: data_po.ripeness }
      tmp_itemlist.codeOutbound = { label: data_po.outboundTypeCode, value: data_po.outboundTypeCode }
      tmp_itemlist.codeInbound = { label: data_po.inboundTypeCode, value: data_po.inboundTypeCode }
      tmp_itemlist.qty = parseFloat(data_po.qty) || 0
      tmp_itemlist.pack = parseFloat(data_po.pack) || 0
      tmp_itemlist.reason = data_po.outboundReason
      po_list[index_po_code].itemList.push(tmp_itemlist);
    })
    tmp.skuUuid = data.skuUuid;
    tmp.skuNumber = data.skuNumber;
    tmp.unitOfMeassures = data.unitOfMeassures;
    tmp.photo = data.photo;
    tmp.poList = po_list;
    po_array_fix.push(tmp);
  })

  //final
  jobHeader[0].jobPost.list = po_array_fix;
  // let ret = await submitMovementCsv({ dataCsv: jobHeader, dispatch });
  let ret = await submitCsv({ dataCsv: jobHeader, setSubmitStatus, dispatch, transaction: 'movement' });
  setIsShowModal(false);
  setIsSubmitText("Submit");

  // setActionButtonTitle('Done');
  // setCancelButtonTitle('');
  // setMsg1(fileName + ' upload successfull.');
  // setMsg2('Would you import another file?');
  // setTitle('Success');
  // setActionButtonClass('bg-green background ml-0');
  // setColourTitle('green'); 
  // setModalShow(true);

};